.DetailKeyValue {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 5px ;
    margin: -1px 0;
}

.DetailKeyValue .DetailValue {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 10px;
}

.DetailKeyValue .DetailKey {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 10px;
}

