.FilterListWrapper {
    height: inherit;
    margin: 5px;
}

.FilterInput {
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 10px;
}

.FilterList {
    display: flex;
    flex-wrap: wrap;
    border-left: solid 1px #ddd;
    border-right: solid 1px #ddd;
    border-bottom: solid 1px #ddd;
    background: linear-gradient(45deg, #e5e5e5 10%, #f5f5f5 50%);;
    padding: 10px;
    height: calc(100% - 100px);
    overflow: auto;
}