@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: Lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
  overflow: hidden;
}

html {
  height:100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height:100%;
}

.contentPane {
    color: #333;
    height: calc(100% - 63px);
    margin: 64px 0 0;
    width:100%;

}
.content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow-x: auto;
    height: 100%;
    width: inherit;
}
.drawer {

}
.drawer .listItem {
    color: #282c34;
    text-decoration: none;
}

.drawer .small {
    font-size: 60%;
    font-weight: 300;
}

.drawer .thin {
    font-weight: 300;
}


.CreateRuleForm {
    margin-top: 10px;
}
DIV.formRow {
    display: flex;
}

DIV.description {
    margin-bottom: 10px;
}


.Description {
    color: #eee;
}
.ScopeDesc {
    margin-bottom: 20px;
}
.PopOver { }

.CreatePopOver {
    background: linear-gradient(45deg, #1565c0 30%, #283593 90%);
    width: 800px;
    height: 500px;
    position:relative;
    overflow-x: hidden;
    opacity: unset;
}

.PopOverTabs {
    flex-grow: 1;
    width: 800px;
    z-index: 2;
    position: absolute;
    top:0;
    background: transparent;
    box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.75);
}

.PopOverTabContent {
    margin-top: 48px;
    padding: 10px 30px 10px 0px;
    height: calc(100% - 48px);
    overflow-x: auto;
}
.QueryResponseColumns {
    width:90%;
    display: flex;
    height: 90%;
    padding: 10px;
    margin: 10px;
    position: relative;
}

.QueryResponseColumns .QueryResponseLeftColumn {
    width: 50%;
    height: 100%;
    background: whitesmoke;
    border: solid 1px #ccc;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestInfo {
    padding: 5px;
    width: 100%;
    background: whitesmoke;
    border-bottom: solid 1px #ccc;
    display: flex;
    align-items: center;
    color: #666;
    height: 42px;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestInfo SPAN {
    margin-right: 10px;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestBody {
    padding: 10px;
    width: 100%;
    height: calc(100% - 68px);
    overflow-y: auto;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestStatus {
    padding: 3px;
    width: 100%;
    background: whitesmoke;
    border-top: solid 1px #ccc;
    color: #666;
    height: 26px;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseInfo {
    padding: 5px;
    width: 100%;
    background: whitesmoke;
    border-bottom: solid 1px #ccc;
    height: 42px;
    display: flex;
    align-items: center;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseBody {
    padding: 10px;
    width: 100%;
    height: calc(100% - 67px);
    overflow-y: auto;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseStatus {
    padding: 3px;
    width: 100%;
    background: whitesmoke;
    border-top: solid 1px #ccc;
    color: #666;
    height: 26px;
}

.QueryResponseColumns .QueryResponseRightColumn {
    width: 50%;
    height: 100%;
    overflow: hidden;
    background: whitesmoke;
    border: solid 1px #ccc;
}
.QueryResponseColumns .RunQuery {
    cursor: pointer;
    align-items: center;
    justify-content: center;
    position: absolute;
    width:50px;
    height: 50px;
    top: 50%;
    left: 50%;
    display: flex;
    text-align: center;
    transform: translate(-50%, -50%);

    background: linear-gradient(45deg, #d81b60 30%, #880e4f 90%);
    color: white;
    border-radius: 50%;
}
.QueryResponseColumns .DisableQuery {
    align-items: center;
    justify-content: center;
    position: absolute;
    width:50px;
    height: 50px;
    top: 50%;
    left: 50%;
    display: flex;
    text-align: center;
    transform: translate(-50%, -50%);

    background: linear-gradient(45deg, #666 30%, #333 90%);
    color: white;
    border-radius: 50%;
}


.MethodTag {
    padding: 4px;
    margin: 2px 6px 2px 2px;
    border-radius: 4px;
    max-width: 100px;
}
.GETTag {
    background: #1565c0;
    color: white;
}

.POSTTag {
    background: #FF8E53;
    color: white;
}

.DELETETag {
    background: #d81b60;
    color: white;
}

.PUTTag {
    background: #FF8E53;
    color: black;
}
.StatusCodeTag {
    padding: 4px;
    margin: 2px 6px 2px 2px;
    border-radius: 4px;
    max-width: 100px;
}

.StatusCode200, .StatusCode204 {
    background: #43a047;
    color: white;
}

.StatusCode500 {
    background: #d81b60;
    color: white;
}

.StatusCode404 {
    background: #d81b60;
    color: white;
}
.CenteredCircularProgress {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.PagePane {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: inherit;
    width: inherit;
}

.EventAvatar {
    background: linear-gradient(45deg, #ffc107 30%, #FF8E53 90%);
    color: white !important;
    font-size: 1em !important;
}
.FilterListWrapper {
    height: inherit;
    margin: 5px;
}

.FilterInput {
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 10px;
}

.FilterList {
    display: flex;
    flex-wrap: wrap;
    border-left: solid 1px #ddd;
    border-right: solid 1px #ddd;
    border-bottom: solid 1px #ddd;
    background: linear-gradient(45deg, #e5e5e5 10%, #f5f5f5 50%);;
    padding: 10px;
    height: calc(100% - 100px);
    overflow: auto;
}
.ListDetailView {
    width:90%;
    display: flex;
    height: 90%;
    padding: 10px;
    margin: 10px;
}

.ListDetailView .ListDetailViewLeftColumn {
    width: 50%;
    height: 100%;
}

.ListDetailView .ListDetailViewRightColumn {
    padding: 10px;
    width: 50%;
    height: 100%;
    overflow-y: auto;
}
.ScopeAvatar {
    background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    color: white !important;
    font-size: 1em !important;
}
.RulePane {
    margin-top: 20px;
    margin-left: 50px;
}

.RuleDescription {
    font-weight: 300;
}

.RuleRawData H1 {
    font-size: 140%;
    font-weight: 300;
    text-transform: uppercase;
}

.RuleSwitch {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 0 5px ;
    margin: -1px 0;
}
.RuleSwitch DIV.RuleSwitchLabel {
    padding-left: 10px;
}
.RuleSwitch .RuleSwitchDescription{
    line-height: 2em;
}
.RuleSwitch .RuleSwitch{
    line-height: 1em;
}

.SmallTitle {
    font-size: 12px;
}

.SmallTitle PRE {
    margin:0
}
.DetailBox {
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 10px;
}
.MiniHeader {
    text-transform: uppercase;
    font-size: 10px;
    width: 100%;
    border-bottom: gray 1px solid;
    color:#282c34;
    padding: 8px;
    margin: 10px 0 1px 0;
}
.DetailHeader {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.DetailHeader  .DetailLabel {
    text-transform: uppercase;
    font-size: 180%;
    margin: 10px 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}



.DetailKeyValue {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: #f5f5f5;
    border: solid 1px #ddd;
    padding: 5px ;
    margin: -1px 0;
}

.DetailKeyValue .DetailValue {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 10px;
}

.DetailKeyValue .DetailKey {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 10px;
}


.RightTabScroller {
    height: calc(100% - 97px);
    overflow-y: auto;
    padding: 5px;
}
.DetailActionButtons {
    padding: 10px;
    display: flex;
    justify-items: right;
    flex-direction: row;
}
.RuleAvatarEnabled {
    background: linear-gradient(45deg, #43a047 30%, #7cb342 90%);
    color: white !important;
    font-size: 1em !important;
}

.RuleAvatarDisabled {
    background: linear-gradient(45deg, #d81b60 30%, #880e4f 90%);
    color: white !important;
    font-size: 1em !important;
}
.SegmentAvatarEnabled {
    background: linear-gradient(45deg, #63a047 30%, #7cb342 90%);
    color: white !important;
    font-size: 1em !important;
}

.SegmentAvatarDisabled {
    background: linear-gradient(45deg, #d81b60 30%, #880e4f 90%);
    color: white !important;
    font-size: 1em !important;
}
.TrackerCode {
    width: 100%;
    overflow-x: auto;
}
.Head {
    display: flex;
    flex-direction: row;
    align-items: center;
}

DIV.Description {
    line-height: 3em;
    color: #282c34;
}

.Head H1 {
    margin: 0 0 0 10px;
    font-size: 160%;
    font-weight: 300;
    text-transform: uppercase;
}
.ProfileAvatar {
    background: linear-gradient(45deg, #1565c0 30%, #283593 90%);
    color: white !important;
    font-size: 1em !important;
}
