.PopOver { }

.CreatePopOver {
    background: linear-gradient(45deg, #1565c0 30%, #283593 90%);
    width: 800px;
    height: 500px;
    position:relative;
    overflow-x: hidden;
    opacity: unset;
}

.PopOverTabs {
    flex-grow: 1;
    width: 800px;
    z-index: 2;
    position: absolute;
    top:0;
    background: transparent;
    -webkit-box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.75);
    box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.75);
}

.PopOverTabContent {
    margin-top: 48px;
    padding: 10px 30px 10px 0px;
    height: calc(100% - 48px);
    overflow-x: auto;
}